export default function translate(id){
    switch(Math.floor(id/100)){
        case 2:
            return ''
        case 3:
            return ''
        case 5:
            return ''
        case 6:
            return ''
        case 7:
            return ''
        case 8:
            if(id===800){
                return ''
            }else{
                return ''
            }
        default:
            return ''
    }
}